import React from 'react';
import ReactDOM from 'react-dom';
import Draggable from 'react-draggable';
import dCss from './draggable.module.css'
import icon_point from '../../../assets/svg/move-svgrepo-com.svg'
import UserPhotoLoad from "../UserPhotoLoad";

class DraggableGlass extends React.Component {
    constructor(props) {
        super(props)
        this.image = props.image
        this.imgWidth = 150 // - для контроля размера
    }

    render() {
        return (
            <Draggable
                axis="both"
                handle=".handle"
                defaultPosition={{x: 0, y: 0}}
                position={null}
                scale={1}
                bounds={'parent'}
                onStart={this.handleStart}
                onDrag={this.handleDrag}
                onStop={this.handleStop}>
                <div className={dCss.dragContainer}>
                        <img className={dCss.tryImage} src={this.image} width={`${this.imgWidth}`} alt=""/>
                        <div className="handle">
                            <svg width="64px" height="64px" viewBox="0 0 16 16" xmlns="http://www.w3.org/2000/svg"
                                 className={dCss.pointSvg}>
                                <g>
                                    <path d="M7.463.057A.748.748 0 007.22.22l-2 2a.75.75 0 001.06 1.06L7 2.56V7H2.56l.72-.72a.75.75 0 00-1.06-1.06l-2 2a.748.748 0 000 1.06l2 2a.75.75 0 101.06-1.06l-.72-.72H7v4.44l-.72-.72a.75.75 0 00-1.06 1.06l2 2a.748.748 0 001.06 0l2-2a.75.75 0 10-1.06-1.06l-.72.72V8.5h4.44l-.72.72a.75.75 0 101.06 1.06l2-2a.748.748 0 000-1.06l-2-2a.75.75 0 10-1.06 1.06l.72.72H8.5V2.56l.72.72a.75.75 0 101.06-1.06l-2-2a.748.748 0 00-.817-.163z"></path>
                                </g>
                            </svg>
                        </div>
                </div>
            </Draggable>
        );
    }
}

export default DraggableGlass
