import React, {Component} from "react";
import Container from "@mui/material/Container";
import loadImg from './LoadPhoto.module.css'
import Button from "@mui/material/Button";
import FileUploadIcon from '@mui/icons-material/FileUpload';
import defaultPhoto from '../../assets/image/defaultMan.png'

class UserPhotoLoad extends Component {
    constructor(props) {
        super (props);
        this.state = {
            image: defaultPhoto
        };

        this.onImageChange = this.onImageChange.bind (this);
    }

    onImageChange = event => {
        if (event.target.files && event.target.files[0]) {
            let img = event.target.files[0];
            this.setState ({
                image: URL.createObjectURL (img)
            });
        }
    };

    render() {
        return (
            <Container>
                <div className={loadImg.userPhotoBox}>
                    <img src={this.state.image}/>
                </div>
                <br/>
                <Container className={loadImg.uploadBtn}>
                    <Button variant={'contained'}>
                        <label className="custom-file-upload">
                            <input type="file" name="myImage" onChange={this.onImageChange}/>
                            <FileUploadIcon/><br/>
                        </label>
                    </Button>
                </Container>
            </Container>

        );
    }
}

export default UserPhotoLoad;